.photo__wrapper
{
  width:320px;
  margin-left:auto;
  margin-right:auto;
  background-color:white;
  overflow: hidden;
}
.photo__benefits {
  width:320*1px;
  max-width:1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  overflow: hidden;
  border-top:1px solid #e5e5e5;
  &--last
  {
    border-bottom:1px solid #e5e5e5;
  }
}

.photo__picture {
  display: block;
  width: 280px;
  height: 280px;
  margin:0px;
}

.photo__headline {
  @extend .offers__headline;
  display: block;
  font-size: 14px;
  margin-top: 22px;
  line-height: 13px;
  margin-left: 19px;
}

.photo__info {
  @extend .photo__headline;
  font-weight: 400;
  margin-top: 11px;
  line-height: 10px;
  color: #333333;
  height:13px;
  text-transform: capitalize;
}

.photo__button {
  display: inline-block;
  width: 23px;
  height: 21px;
  padding: 0;
  background-color: rgb(0,0,0);
  background-image:url("img/like-icon.svg");
  background-size:100% 100%;
  border: 0;
  margin-top:7px;
  margin-left:19px;
  margin-bottom:21px;
  position: relative;
}

.photo__button-img {
  width: 100%;
  height: 100%;
}

.photo__likes {
  display: inline-block;
  position: relative;
  margin-top:2px;
  margin-left: 10px;
  @include font(14px, 700, black, left);
  line-height: 12px;
  height:11px;
}
.photo__link
{
  display: block;
  width:280px;
  margin-top:22px;
  margin-left:auto;
  margin-right:auto;
}
